.verify_owner-address {
  margin-bottom: 20px;
}

.verify_owner-id {
  margin-bottom: 60px;
}

.verify_owner-input {
  width: 300px;
  height: 50px;
  border: 1px solid #086E7D;
  border-radius: 6px;
  padding-left: 20px;
  font-family: "GT Walsheim Pro";
  font-size: 16px;
  margin-bottom: 20px;
}

.verify_owner-button {
  width: 300px;
  height: 55px;
  background: #086E7D;
  border-radius: 6px;
  border: none;
  font-family: "GT Walsheim Pro";
  font-size: 16px;
  color: #FFFFFF;
  cursor: pointer;
  transition: background 1s;
}

.verify_owner-button:hover {
  background: #06525d;
}

.verify_owner-button:disabled {
  background: #677274;
  cursor: not-allowed;
}

.verify_owner-search {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.success {
  color: #05B102;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 50px;
}

.error {
  color: #E01919;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 50px;
}

.input_error {
  border: 1px solid #E01919;
}